body, p, h1, h2, h4, h5 {
  margin: 0;
  padding: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  display: block;
  position: relative;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
}

.card__name {
  color: #060b35;
  margin-top: 0;
  margin-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__review-stars {
  display: flex;
}

.stars-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars-4 .stars-star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__review-stars > .stars-star:not(:last-child) {
  margin-right: 4px;
}

.card__reviewcounter-reviews {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-name {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card:hover .card__button {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: all .3s;
}
